.toggle {
    width: 30px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    box-sizing: border-box;
    cursor: pointer;
}

.toggle div {
    width: 80%;
    height: 3px;
    background-color: white;
}


@media (min-width: 500px) {
    .toggle {
        display: none;
    }
}