.Label {
    font-weight: bold;
}

.InputElement{
    border-radius: 5px;
    border: 1px solid rgb(226, 184, 184);
    width: 100%;
    padding: 8px;
}

.InputElement:focus {
    background-color: lightblue;
}

@media (max-width : 550px){
    .Label {
        font-size: small;
    }
}

    